@use "global" as *;

.blog-card {
    width: rem(296);
    border-radius: rem(10);
    filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.25));
}

.blog-card__body {
    padding: rem(20);
}

.blog-card__title {
    font-size: rem(20);
    line-height: 1.45;
}

.blog-card__text {
    margin-top: rem(10);
    line-height: 1.4375;
}