@use "global" as *;

.news {
    background: #F1F0F0;
}

.news__inner {
    margin-top: rem(50);
    padding-top: rem(14);
    padding-bottom: rem(65);

    @include mq('md') {
        margin-top: rem(40);
        padding-top: rem(80);
        padding-bottom: rem(90);
    }
}

.news__text {
    margin-top: rem(50);

    @include mq('md') {
        margin-top: rem(75);
    }
}

.news__items {
    margin-top: rem(28);
    display: grid;
    justify-content: center;

    @include mq('md') {
        margin-top: rem(37);
        display: flex;
    }
}

.news__item {
    background: #AF9E97;

    .card__title {
        color: #AF9E97;
    }

    &+& {
        margin-top: rem(27);
    }

    @include mq('md') {
        max-width: calc(33.3333% - rem(20) * 2 / 3);
        width: 100%;

        &+& {
            margin-top: 0;
            margin-left: rem(20);
        }

        &:hover {
            background: $white;

            .card__body {
                background: #AF9E97;
                color: $white;
            }

            .card__title {
                color: $white;
            }
        }
    }
}

.news__button {
    margin-top: rem(37);
    text-align: center;

    @include mq('md') {
        margin-top: rem(47);
    }

    .section__button {
        &:hover {
            background: $white;
            color: $black;
            border: rem(2) solid $black;
        }
    }
}