.blog__item {
  background: #d2d2d2;
}
.blog__item .blog-card__img {
  position: relative;
  width: 100%;
}
.blog__item .blog-card__img::before {
  content: "";
  display: block;
  padding-top: 74.3243%;
}
.blog__item .blog-card__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.blog__item + .blog__item {
  margin-top: 1.375rem;
}
@media screen and (min-width: 768px) {
  .blog__item {
    max-width: calc(33.3333% - 2.3333333333rem);
    width: 100%;
    overflow: hidden;
    position: relative;
    z-index: 1;
  }
  .blog__item::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #3D593D;
    transform: scale(0, 1);
    transform-origin: left top;
    transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
    z-index: -1;
  }
  .blog__item:hover {
    color: #d2d2d2;
  }
  .blog__item:hover::after {
    transform: scale(1, 1);
  }
  .blog__item + .blog__item {
    margin-top: 0;
    margin-left: 3.5rem;
  }
}

