.staff-blog__item {
  background: #f7f7f7;
}
.staff-blog__item .blog-card__img {
  position: relative;
  width: 100%;
}
.staff-blog__item .blog-card__img::before {
  content: "";
  display: block;
  padding-top: 67.5675%;
}
.staff-blog__item .blog-card__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.staff-blog__item + .staff-blog__item {
  margin-top: 2.25rem;
}
@media screen and (min-width: 768px) {
  .staff-blog__item {
    margin-top: 2.5rem;
    max-width: calc(33.3333% - 2.3333333333rem);
    width: 100%;
    overflow: hidden;
    position: relative;
    transition-duration: 0.4s;
    z-index: 2;
  }
  .staff-blog__item::after {
    content: "";
    position: absolute;
    display: block;
    top: 50%;
    left: 0;
    right: 0;
    width: 100%;
    height: 0;
    margin: auto;
    padding-top: 100%;
    background: #818A7D;
    border-radius: 50%;
    opacity: 0;
    z-index: -1;
    pointer-events: none;
    transform: translateY(-50%) scale(0.1);
    transition: opacity 0.5s, transform 0s;
    transition-delay: 0s, 0.4s;
  }
  .staff-blog__item:hover {
    color: #000;
  }
  .staff-blog__item:hover::after {
    opacity: 1;
    transform: translateY(-50%) scale(2.2);
    transition-delay: 0s;
    transition: opacity 0.8s, transform 0.6s ease-in-out;
  }
  .staff-blog__item + .staff-blog__item {
    margin-left: 3.5rem;
  }
  .staff-blog__item:nth-child(3n+1) {
    margin-left: 0;
  }
  .staff-blog__item:nth-child(-n+3) {
