@use "global" as *;

.staff-blog {
    background: linear-gradient(180deg, #fff 0%, #fff 4%, rgba(175, 158, 151, 0.6)4%, rgba(175, 158, 151, 0.6)100%);

    @include mq('md') {
        background: linear-gradient(180deg, #fff 0%, #fff 33%, rgba(175, 158, 151, 0.6)33%, rgba(175, 158, 151, 0.6)78%, #fff 78%, #fff 100%);
    }
}

.staff-blog__inner {
    margin-top: rem(40);
    padding-bottom: rem(35);

    @include mq('md') {
        margin-top: rem(120);
    }
}

.staff-blog__title {
    text-align: center;
}

.staff-blog__text {
    margin-top: rem(60);

    @include mq('md') {
        margin-top: rem(63);
    }
}

.staff-blog__items {
    margin-top: rem(40);
    display: grid;
    justify-content: center;

    @include mq('md') {
        display: flex;
        flex-wrap: wrap;
        margin-top: rem(30);
    }
}

.staff-blog__item {
    background: $white;

    .blog-card__img {
        position: relative;
        width: 100%;

        &::before {
            content: '';
            display: block;
            padding-top: 67.5675%;
        }

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &+& {
        margin-top: rem(36);
    }

    @include mq('md') {
        margin-top: rem(40);
        max-width: calc(33.3333% - rem(56) * 2 / 3);
        width: 100%;
        overflow: hidden;
        position: relative;
        transition-duration: .4s;
        z-index: 2;

        &::after {
            content: '';
            position: absolute;
            display: block;
            top: 50%;
            left: 0;
            right: 0;
            width: 100%;
            height: 0;
            margin: auto;
            padding-top: 100%;
            background: #818A7D;
            border-radius: 50%;
            opacity: 0;
            z-index: -1;
            pointer-events: none;
            transform: translateY(-50%) scale(0.1);
            transition: opacity .5s, transform 0s;
            transition-delay: 0s, .4s;
        }

        &:hover {
            color: #000;

            &::after {
                opacity: 1;
                transform: translateY(-50%) scale(2.2);
                transition-delay: 0s;
                transition: opacity .8s, transform .6s ease-in-out;
            }
        }
        
        &+& {
            margin-left: rem(56);
        }

        &:nth-child(3n+1) {
            margin-left: 0;
        }

        &:nth-child(-n+3) {
            margin-top: 0;
        }
    }
}

.staff-blog__button {
    margin-top: rem(40);
    text-align: center;

    @include mq('md') {
        margin-top: rem(35);
    }

    .section__button {
        position: relative;
        display: inline-block;
        transition: all .3s;
        
        &:hover {
            top: rem(-10);
        }
    }
}