@use "global" as *;

.card {
    max-width: rem(320);
    border-radius: rem(10);
}

.card__link {
    height: 100%;
    display: flex;
    flex-direction: column;
    
}

.card__img {
    position: relative;
    width: 100%;

    &::before {
        content: '';
        display: block;
        padding-top: 62.5%;
    }

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.card__body {
    margin-left: rem(50);
    background: $white;
    padding: rem(20) rem(20) rem(25) rem(10);
    border-bottom-right-radius: rem(10);
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.card__title {
    font-size: rem(20);
    line-height: 1.45;
}

.card__text {
    margin-top: rem(10);
    line-height: 1.4375;
}