@use "global" as *;

.works {
    background: linear-gradient(90deg, #fff 0%, #fff 30.6666%, rgba(80, 92, 75, 0.5) 30.6666%, rgba(80, 92, 75, 0.5) 100%);

    @include mq('md') {
        background: linear-gradient(90deg, #fff 0%, #fff 50%, rgba(80, 92, 75, 0.5) 50%, rgba(80, 92, 75, 0.5) 100%);
    }
}

.works__inner {
    margin-top: rem(50);
    padding-top: rem(10);
    padding-bottom: rem(100);

    @include mq('md') {
        margin-top: rem(80);
        padding-top: rem(140);
    }
}

.works__title {
    text-align: right;
}

.works__text {
    margin-top: rem(30);
}

.swiper {
    margin: 0 calc(50% - 50vw);
    width: 100vw;
}

.works__items {
    margin-top: rem(18);
    padding-left: rem(70);
    
    @include mq('md') {
        margin-top: rem(80);
        padding-left: 0;
    }
}

.works__item {
    background: #6E6B5C;
    
    .card__title {
        color: #6E6B5C;
        text-align: center;
    }

    .card__body {
        overflow: hidden;
        position: relative;
        z-index: 1;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #6E6B5C;
            transform: scale(1,0);
            transform-origin: left top;
            transition: .2s cubic-bezier(0.45, 0, 0.55 ,1);
            z-index: -1;
        }

        &:hover {
            .card__title {
                color: $white;
            }

            &::after {
                transform: scale(1,1);
            }
        }
    }
}

.works__button {
    margin-top: rem(57);
    text-align: center;

    @include mq('md') {
        margin-top: rem(81);
    }

    .section__button {
        position: relative;
        display: inline-block;
        transition: all .3s;
        
        &:hover {
            top: rem(-10);
        }
    }
}