@use "global" as *;

.section__inner {
  width: 100%;
  padding-right: $padding-sp;
  padding-left: $padding-sp;
  margin-right: auto;
  margin-left: auto;
  @include mq("md") {
    max-width: $inner + strip-unit($padding-pc) * 2;
    padding-right: $padding-pc;
    padding-left: $padding-pc;
  }
}

.section__title {
  font-weight: $bold;
  font-size: rem(32);
  display: inline-block;
  position: relative;
  text-transform: uppercase;

  @include mq('md') {
    font-size: rem(48);
  }
}

.section__sub-title {
  border-bottom: rem(2) solid #005700;
  font-weight: $normal;
  font-size: rem(16);
  white-space: nowrap;
  position: absolute;
  bottom: rem(-23);
  left: 50%;
  transform: translateX(-50%);

  @include mq('md') {
    font-size: rem(24);
    bottom: rem(-35);
  }
}

.section__text {
  line-height: 1.4375;
  text-align: center;
  max-width: rem(537);
  margin-left: auto;
  margin-right: auto;
}

.section__button {
  font-weight: $bold;
  font-size: rem(24);
  padding: rem(10) rem(80);
  border-radius: rem(20);
  text-transform: uppercase;

  @include mq('md') {
    font-size: rem(32);
    padding: rem(10) rem(100);
  }

  &--black {
    background: $black;
    color: $white;
  }

  &--white {
    background: $white;
    color: $black;
    border: rem(2) solid $black;
  }
}