@use "global" as *;

.blog {
    background: linear-gradient(90deg, rgba(80, 92, 75, 0.5)0%, rgba(80, 92, 75, 0.5)69.3333%, #fff 69.3333%, #fff 100%);

    @include mq('md') {
        background: linear-gradient(90deg, rgba(80, 92, 75, 0.5)0%, rgba(80, 92, 75, 0.5)50%, #fff 50%, #fff 100%);
    }
}

.blog__inner {
    padding-top: rem(40);
    padding-bottom: rem(110);

    @include mq('md') {
        padding-top: rem(100);
    }
}

.blog__title {
    text-align: center;
}

.blog__text {
    margin-top: rem(40);

    @include mq('md') {
        margin-top: rem(80);
    }
}

.blog__items {
    margin-top: rem(20);
    display: grid;
    justify-content: center;

    @include mq('md') {
        margin-top: rem(48);
        display: flex;
    }
}

.blog__item {
    background: #d2d2d2;

    .blog-card__img {
        position: relative;
        width: 100%;

        &::before {
            content: '';
            display: block;
            padding-top: 74.3243%;
        }

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &+& {
        margin-top: rem(22);
    }

    @include mq('md') {
        max-width: calc(33.3333% - rem(56) * 2 / 3);
        width: 100%;
        overflow: hidden;
        position: relative;
        z-index: 1;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #3D593D;
            transform: scale(0,1);
            transform-origin: left top;
            transition: .2s cubic-bezier(0.45, 0, 0.55 ,1);
            z-index: -1;
        }

        &:hover {
            color: #d2d2d2;

            &::after {
                transform: scale(1,1);
            }
        }
        
        &+& {
            margin-top: 0;
            margin-left: rem(56);
        }
    }
}

.blog__button {
    margin-top: rem(35);
    text-align: center;

    @include mq('md') {
        margin-top: rem(68);
    }

    .section__button {
        &:hover {
            background: $white;
            color: $black;
            border: rem(2) solid $black;
        }
    }
}